/* css reset */
*,*::after,*::before{box-sizing: border-box;}
*{margin: 0;padding: 0;}
ul,ol{list-style:none;}
a{color: inherit;text-decoration: none;}
img{max-width: 100%;max-height: 100%;}

/* style */
body {
    overflow: hidden;
    background-color: #eee;
}
.globalHeader {
    margin: 20px;
}
@media (min-width:500px) {
    .globalHeader{
        margin: 60px 0;
    }
}
.searchForm {
    display: flex;
    justify-content: space-between;
}
@media (min-width:500px) {
    .searchForm {
        /* border:1px solid blue; */
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
.searchForm > input{
    width: 100%;
    margin-right: 10px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 28px; 
    border: none;
    border-radius: 4px;
    background-color: lightblue;
    font-size: 16px;
}
.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}
.siteList {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    /* border: 1px solid red; */
}
@media (min-width:500px) {
    .searchList {
        margin-left: 0;
        margin-right: -60px;
        justify-content: flex-start;
    }
}
.siteList>li {
    margin-bottom: 30px;
}
@media(min-width:500px){}
    .siteList>li {
        margin-right: 25px;
}
.siteList .site{
    /* border: 1px solid red; */
    width: 160px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: #fff;
    border:1px solid #ddd;
    border-radius: 4px;
    padding: 10px 0;
    position: relative;
    cursor: pointer;
}
.siteList .site > .logo{
    /* border: 1px solid red; */
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
}
.siteList .site > .link {
    font-size: 14px;
    margin-top: 4px;
}
.siteList .site .close {
    position: absolute;
    right: 5px;
    top: 5px;
    display: none;
}
.siteList .site:hover .close {
    display: block;
}
.siteList .addButton {
    width: 160px;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #ddd;
    /* border: 1px solid red; */
}
.siteList .addButton .text {
    font-size: 14px;
}
.siteList .addButton .add {
    width: 64px;
    height: 64px;
    margin-top: 4px;
    /* border: 1px solid blue; */
}